import React, {CSSProperties} from "react";

type ButtonProps = {
    type: 'button' | 'submit';
    title: string;
    variant: string;
    onClick?: () => void;
    style?: CSSProperties;
}

const Button = ({ type, title, variant, onClick, style} : ButtonProps) => {
    return(
        <button
            className={`whitespace-nowrap border text-sm font-medium rounded-md ${variant} `}
            type={type}
            onClick={onClick}
            style={style}
        >
            <label className="font-semibold text-sm cursor-pointer">{title}</label>
        </button>
    )
}

export default  Button;