import React, {CSSProperties} from "react";

type InputProps = {
    type: string;
    placeholder:string;
    required?: boolean;
    style?: CSSProperties;
}

const Input = ({type, placeholder, required, style} : InputProps) => {
    return(
     <div>
         <input
             className={`p-3 pl-6 w-full h-full text-sm text-gray-900 rounded-lg border border-gray-300 outline-none justify-center grow`}
             type={type}
             placeholder={placeholder}
             required={required}
             style={style}
         />
     </div>
    )
}

export default Input

